<template>
	<div id="store-pill">
		<public-header title="支付方式"></public-header>
		<div class="content">
			<van-radio-group v-model="store.auto_handel_order">
				<van-cell-group>
					<van-cell title="自动接单方式"/>
					<van-cell title="支付后自动接单" clickable @click="store.auto_handel_order = '1'" class="border-0px">
						<van-radio name="1" />
					</van-cell>
					<van-cell title="不自动接单" clickable @click="store.auto_handel_order = '0'" class="border-0px">
						<van-radio name="0" />
					</van-cell>
					<van-cell title="打印机出单后自动接单(仅飞鹅和喜讯打印机支持)" clickable @click="store.auto_handel_order = '2'" class="no-fix border-0px">
						<van-radio name="2" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title">接单后自动通知配送员</div>
					<iswitch slot="right-icon" :value="store.auto_notice_deliveryer" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="store.auto_notice_deliveryer"></iswitch>
				</van-cell>
				<van-cell>
					<div slot="title">支持开发票</div>
					<iswitch slot="right-icon" :value="store.invoice_status" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="store.invoice_status"></iswitch>
				</van-cell>
				<van-cell>
					<div slot="title">支持代金券抵付</div>
					<iswitch slot="right-icon" :value="store.token_status" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="store.token_status"></iswitch>
				</van-cell>
			</van-cell-group>
			<van-checkbox-group class="margin-10-t" v-model="store.payment">
				<van-cell-group>
					<van-cell title="支付方式" />
					<van-cell
						v-for="(item,index) in payments"
						v-if="item.value != 'peerpay'"
						clickable
						:key="index"
						:title="item.title"
						@click="onTogglePayment(index)"
						class="border-0px"
					>
						<van-checkbox :name="item.value" ref="checkboxes" />
					</van-cell>
				</van-cell-group>
			</van-checkbox-group>
			<div class="padding-15">
				<van-button size="normal" :disabled="!islegal" block @click="onSubmit" class="font-16 bg-info">提交</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				payments: [],
				store: {
					auto_handel_order: 0,
					auto_notice_deliveryer: 0,
					invoice_status: 0,
					token_status: 0,
					payment: [],
				},
				showPreLoading: true,
				islegal: false
			}
		},
		components: {
			publicHeader,
			iswitch
		},
		methods: {
			onTogglePayment(index) {
				this.$refs.checkboxes[index].toggle();
			},
			onLoad() {
				var that = this;
				getData({
					url: 'manage/shop/index/pill',
					vue: that,
					data: {},
					success: (res) =>{
						that.islegal = true;
						that.store = Object.assign(that.store, res.store)
						this.payments = [...res.payments];
					}
				});
			},
			onSubmit() {
				var that = this;
				if(!that.islegal) {
					return false;
				}
				that.islegal = false;
				setData({
					url: 'manage/shop/index/pill',
					vue: that,
					data: {
						store: JSON.stringify(that.store),
					},
					redirect: that.util.getUrl({path: 'pages/shop/index'}),
					fail: function(res) {
						that.util.$toast(res);
						that.islegal = true;
						return;
					}
				})
			},
			jsToggleSwitch(res) {
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value
				});
			}
		},
		created() {
			if(this.$route.query && this.$route.query.id > 0) {
				this.id = parseInt(this.$route.query.id)
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#store-pill .van-cell.no-fix .van-cell__title{
		flex: none;
	}
	#store-pill .iswitch{
		height: 24px;
	}
</style>


